.catalog {
   padding: 25px 0 88px;
   &__breadcrumbs {
      margin-bottom: 70px; }
   &__title {
      margin-bottom: 32px; }
   &__filters {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      font-size: 0;
      letter-spacing: 0;
      padding: 30px 0 80px;
      &-select {
         display: inline-block;
         vertical-align: middle;
         &:not(:nth-child(3n)) {
            margin-right: 30px; }
         &:nth-child(3) ~ & {
            margin-top: 44px; } } }
   &__row {
      display: flex;
      flex-wrap: wrap; }
   &__item {
      flex: 0 0 33.33333%;
      max-width: 390px;
      &:not(:nth-child(3n)) {
         border-right: 0; }
      &:not(:nth-last-child(-n + 3)) {
         border-bottom: 0; } } }

.product {
   padding: 25px 0 50px;
   color: #4b5666;
   &__inner {
      max-width: 1070px; }
   &__breadcrumbs {
      margin-bottom: 87px; }
   &__row {
      margin: 0 -15px;
      @include clr;
      & + & {
         margin: 52px -15px 0; } }
   &__col {
      padding: 0 15px;
      width: 510px;
      &:nth-child(1) {
         float: left; }
      &:nth-child(2) {
         float: right; } }
   &__view-block {}
   &__features {
      display: flex;
      flex-wrap: wrap;
      margin: 37px -15px 0;
      &-item {
         display: flex;
         align-items: flex-start;
         flex: 0 0 50%;
         max-width: 255px;
         padding: 0 15px;
         &:nth-child(2) ~ & {
            padding-top: 45px; }
         &-icon-wrap {
            flex: 0 0 50px;
            margin-right: 23px; }
         p {
            font-size: 14px;
            line-height: 22px;
            color: #4b5f72;
            letter-spacing: 0.28px; } } }
   &__title {
      margin-top: -8px;
      margin-bottom: 50px;
      font-size: 32px;
      letter-spacing: 0.64px;
      line-height: 41px; }
   &__price {
      display: block;
      margin-bottom: 50px;
      @include f('ms700', 30, 30);
      color: $color-3;
      letter-spacing: 0.6px; }
   &__info {
      &-item {
         &:not(:last-child) {
            margin-bottom: 20px; } }
      &-title {
         margin-bottom: 27px;
         @include f('ms700', 18, 24);
         letter-spacing: 0.36px; }
      &-list {
         column-count: 2;
         column-gap: 30px;
         column-width: 200px;
         li:nth-child(3) ~ li {
            margin-left: 40px; }
         li {
            position: relative;
            padding-left: 37px;

            &:not(:last-child) {
               margin-bottom: 9px; }
            &::before {
               position: absolute;
               top: 12px;
               left: 3px;
               width: 23px;
               height: 2px;
               background-color: #8398ad;
               content: ''; } } } }
   &__actions {
      &-btn {
         width: 100%;
         &:not(:last-child) {
            margin-bottom: 45px; } } }

   &__descr {
      margin-top: 110px;
      &-item {
         &:not(:last-child) {
            margin-bottom: 75px; } }
      &-title {
         margin-bottom: 35px;
         @include f('ms700', 18, 24);
         letter-spacing: 0.36px; }
      &-sub-title {
         margin: 50px 0 20px;
         @include f('ms600');
         letter-spacing: 0.32px; }

      &-text {
         p {
            &:not(:last-child) {
               margin-bottom: 24px; } } }
      &-list {} } }

.assort {
   padding: 50px 0 80px;
   &__section-top {
      margin-bottom: 50px; }
   &__slider {
      border-right: 2px solid #e1e6ee;
      .slider-btn {
         top: 53%;
         &_prev {
            left: -72px; }
         &_next {
            right: -72px; } } }
   &__item {
      border-right: 0; } }
